
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import vHeader from '@/components/HeaderSearch'
import GoodsList from '@/components/GoodsList'
import OfflineAlert from '@/components/OfflineAlert'

export default {
components: {
Tab,
TabItem,
Swiper,
Sticky,
vHeader,
GoodsList,
OfflineAlert
},
data () {
return {
isAttention: false,   // 
tabActive: 0,         // 
tabHeight: 44,        // tab  44 
isShowAlert: false,  // 
carousel: [],         // 
storeData: {},        // 
recommendGoods: [],   // 
offlineInfo: {        // 
address: '',
phone: '',
desc: ''
},
sortName: 'sold_number',              // tab  sold_number()  platform_price() favourable_comment_rate()
platformPriceSortNum: 4,              //    3 4
favourableCommentRateSortNum: 4,      //    3 4
allGoodsParams: {     // 
supplier_id: this.$route.params.id,
page: 1,
'sort[]': 'sold_number:3',
size: 12
},
allGoodsData: []
}
},
activated () {

this.axios.get('/supplier/index', {supplier_id: this.$route.params.id}, res => {
console.log(res, '')
let data = res.data.index
this.storeData = data
this.carousel = data.carousel.map(item => {
return {
url: '/good-detail/' + item.url,
img: item.image
}
})
this.offlineInfo.address = data.district
this.offlineInfo.phone = data.line_supplier_mobile
this.isAttention = data.is_follow
})

this.axios.get('supplier/recommend-second', {supplier_id: this.$route.params.id}, res => {
console.log(res, '')
this.recommendGoods = res.data.recommend_second
})
},
methods: {
onClickTab (index) {
this.tabActive = index
if (index === 0) {

this.tabHeight = 44
} else {

this.tabHeight = 88
this.getAllGoodsData()
}
},
/**
* 
* 
* @param bool
*/
isShow (bool) {
this.isShowAlert = bool
},
getAllGoodsData () {
this.axios.get('/supplier/goods', this.allGoodsParams, res => {
console.log(res, '')
this.allGoodsData = res.data.supplier_goods
})
},
tabHandle (str) {
this.sortName = str
switch (str) {      // 
case 'platform_price':

this.platformPriceSortNum = this.platformPriceSortNum === 4 ? 3 : 4     
this.favourableCommentRateSortNum = 4                                   
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.platformPriceSortNum
break
case 'favourable_comment_rate':

this.favourableCommentRateSortNum = this.favourableCommentRateSortNum === 4 ? 3 : 4     
this.platformPriceSortNum = 4                                                           
this.allGoodsParams['sort[]'] = this.sortName + ':' + this.favourableCommentRateSortNum
break
default:
this.allGoodsParams['sort[]'] = this.sortName + ':' + 3
}
this.getAllGoodsData()
},
attentionStore () {

this.axios.post('/user-follow/user-follow-shop', res => {

})
}
}
}



